@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@200;300;400;500;600;700;800;900&family=Poppins:wght@200;300;400;500;600;700;800;900&family=Roboto:wght@300;400;500;700;900&display=swap');
/* Possitiomn and Sizing our Burger Menu */

.bm-burger-button{
    position: fixed;
    width:36px;
    height:36px;
    left:32px;
    top:32px
}

/* Color to our burger */
.bm-burger-bars{
    background:#373a47;
}

/* Color to our burger icon on our hover */
.bm-burger-bars-hover{
    background: #000;
}

/* Size of the cross button */
.bm-cross-button{
    height:24px;
    width:24px
}

/* Color of the cross button */
.bm-cross{
    background: #fff;
}

/* Sidebar wrapper styles */
.bm-menu-wrap{
    position: fixed;
    height:100vh;
    overflow-x:hidden;
    box-shadow: 0px 4px 31px rgba(0,0,0,0.15)
}

.bm-menu{
    background: linear-gradient(39.97deg,#03d6f3 0.79%, #458ff6 79.29%); /* copy pasted from figma */
    padding:0.5em 1.5em 0 0;
    height:100vh;
    overflow:hidden !important;
}

.bm-item-list{
    padding:0.8em
}
.menu-item{
    position:relative;
    font-size:2em;
    text-decoration: none;
    color:#fff;
    font-family:Montserrat;
    font-style:normal;
    font-weight:normal;
    padding:10px
}

.menu-item:before{
    content:'';
    position:absolute;
    width:100%;
    height:0;
    bottom:0;
    left:0;
    background-color:#fff;
    visibility: hidden;
    transition: all 0.5s ease-in-out;
}

.active-item{
    font-weight:500; /* making it bold */
}

.menu-item:hover:before,
.active-item:before{
    visibility: visible;
    height:3px /* making line */
}

